<template>
  <section class="prdoucts_list py-12 theme--dark" id="Products">
    <v-container>
      <v-row justify="end" class="">
        <!-- START:__PRODUCTS_SORTING -->
        <v-col cols="12">
          <h2 class="font-weight-black">All Products</h2>
        </v-col>
        <v-col cols="8" sm="4" md="3">
          <v-select
            dense
            v-model="select"
            :items="items.select"
            outlined
            label="sort by"
          ></v-select>
        </v-col>
      </v-row>
      <!-- START:__PRODUCTS___ -->
      <v-row>
        <v-col cols="12" sm="4" md="3" v-for="(item, i) in pageNumber" :key="i">
          <v-hover v-slot="{ hover }">
            <v-card
              max-width="300px"
              :class="[{ 'on-hover': hover }]"
              class="transparent mx-auto transition-fast-in-fast-out"
              flat
            >
              <v-img
                contain
                :position="imagePosition"
                height="300"
                :src="require('../../assets/images/' + item.src + '.jpg')"
              >
                <v-fade-transition>
                  <v-card
                    v-if="hover"
                    style="height: 100%"
                    class="d-flex v-card--reveal flex-column transition-fast-in-fast-out justify-space-between"
                  >
                    <v-card-text class="d-flex align-end flex-column">
                      <v-btn color="" small class="my-2 transparent" fab
                        ><v-icon color="white">
                          mdi-heart-outline</v-icon
                        ></v-btn
                      >
                      <v-btn
                        router
                        :to="{ name: 'product', params: { id: item.id } }"
                        color=""
                        small
                        fab
                        class="transparent"
                        ><v-icon color="white">mdi-eye-outline</v-icon></v-btn
                      >
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="addToCart(item)" color="error" block>
                        <span>Add to Cart</span>
                        <v-icon right>mdi-cart-outline</v-icon>
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-fade-transition>
              </v-img>
              <v-card-actions class="subtitle-1 py-1 px-0"
                ><v-hover v-slot="{ hover }">
                  <v-list-item
                    style="cursor: pointer"
                    :to="{ name: 'product', params: { id: item.id } }"
                  >
                    <span
                      class="transition-fast-in-fast-out"
                      :class="[hover ? 'primary--text' : '']"
                      >{{ item.title }}</span
                    >
                  </v-list-item>
                </v-hover>
              </v-card-actions>
              <v-card-text class="pt-0 px-0">
                <!-- <v-row> -->
                <div class="d-flex">
                  <v-rating
                    dense
                    size="15"
                    readonly
                    :value="item.rating"
                    half-increments
                  ></v-rating>
                  <div class="grey--text ml-4">{{ item.rating }}</div>
                </div>
                <!-- </v-row> -->
                <v-card-subtitle class="pl-0 px-0 py-1"
                  >$<b>{{ item.price }}</b></v-card-subtitle
                >
              </v-card-text>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
      <!-- START:__PRODUCTS::PAGINATION___ -->
      <v-pagination circle v-model="page" :length="pageLength"></v-pagination>
      <!-- END:________________________ -->
    </v-container>
  </section>
</template>

<script>
import data from "@/db/data.json";
import { mapActions, mapState } from "vuex";
export default {
  data: () => ({
    select: "Default Sorting",
    page: 1,
    items: data,
    limitCard: 8,
    dialog: true,
    value: true,
    imagePosition: "left",
  }),
  created() {
    if (window.matchMedia("(max-width:599px)").matches) {
      this.imagePosition = "left";
    } else {
      this.imagePosition = "left";
    }
    window.onresize = () => {
      if (window.matchMedia("(max-width:599px)").matches) {
        this.imagePosition = "left";
      } else {
        this.imagePosition = "left";
      }
    };
  },
  computed: {
    sortResult() {
      let price = [...this.items.latest];
      return this.select == "Price"
        ? // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          price.sort((a, b) => b.price - a.price)
        : this.select == "Default Sorting"
        ? this.items.latest
        : this.select == "rating"
        ? price.sort((a, b) => b.rating - a.rating)
        : "";
    },
    pageNumber() {
      return this.sortResult.slice(
        (this.page - 1) * this.limitCard,
        this.page * this.limitCard
      );
    },
    pageLength() {
      return Math.ceil(this.sortResult.length / this.limitCard);
    },
    ...mapState({
      snack: (store) => store.ecommerce.snack,
    }),
  },
  methods: {
    ...mapActions(["A_addToCart"]),
    addToCart(product) {
      let quantity = 1;
      this.A_addToCart({
        product: product,
        quantity: quantity,
      });
    },
    redirectTo(id) {
      this.$router.push({ name: "product", params: { id: id } });
    },
  },
};
</script>

<style lang="scss" scoped>
#Products {
  .v-card {
    transition: opacity 0.4s ease-in-out;
  }

  .v-card:not(.on-hover) {
    opacity: 0.9;
  }
  .on-hover {
    transform: translateY(0px);
  }
}
.v-card--reveal {
  align-items: center;
  top: 0;
  justify-content: center;
  opacity: 0.5;
  position: absolute;
  width: 100%;
  background: linear-gradient(
    180deg,
    hsla(207, 24%, 48%, 0.3),
    hsla(207, 24%, 4%, 1) 95%
  );
}
</style>
